/*
* Custom select
--------------------------------*/
.custom-select {
  font-size: 19px;
  color: black;
  font-family: 'helveticaneuecyrroman';
  text-align: left; }

span.customSelect {
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #bdbdbd;
  border-radius: 6px;
  font-size: 19px;
  color: #4c4a40;
  font-family: 'helveticaneuecyrroman';
  text-align: left;
  height: 45px;
  background: #fff url(../img/svg/dropdown.svg) no-repeat calc(100% - 13px) 70%;
  background-size: 14px; }

span.customSelect .customSelectInner {
  height: 45px;
  padding: 0 30px;
  line-height: 45px;
  white-space: nowrap;
  font-family: 'helveticaneuecyrlight'; }

span.customSelect.custom-select-small {
  height: 34px;
  font-size: 14px;
  vertical-align: middle; }

span.customSelect.custom-select-small .customSelectInner {
  height: 36px;
  line-height: 36px;
  padding: 0 30px 0 16px; }

.custom-checkbox,
.custom-radio {
  display: inline-block;
  vertical-align: middle;
  margin: 0 4px 0 0;
  border-radius: 15px;
  border: 1px solid #8b8b8b;
  width: 15px;
  height: 15px; }

.custom-checkbox.checked,
.custom-radio.checked {
  background: #53ab53;
  border: 1px solid #53ab53; }

.custom-checkbox.checked:before {
  font: normal normal normal 14px/1 FontAwesome;
  color: white;
  left: 1.5px;
  font-size: 12px;
  content: "\f00c";
  position: relative;
  top: -2px; }

.custom-radio.checked:before {
  content: '';
  position: absolute;
  top: 1px;
  left: 1px;
  width: 9px;
  height: 9px;
  border-radius: 13px;
  border: 2px solid #fff; }

